import {useEffect,useState,React} from 'react'
import didyouknow from './icon/didyouknow.png'
import { Facts } from '../../Pages/data';
import './did_you_know.css'
function DidYouKnow(props) {
  const [Loading, setLoading] = useState(false);
  const [Nothing, setNothing] = useState(false);
  const [Fact, setFact] = useState([]);



 
  useEffect(() => {

    // Randomizing the Facts
    setFact(Facts[Math.floor(Math.random() * Facts.length)])
    // For Showing loading state with Didyouknow
    if(props.state === 'Loading'){
      setLoading(true)
    }
    // For Showing Nothing to show  state with Didyouknow

    else if(props.state === 'Nothing'){
setNothing(true)
    }
    
  },[props.state]);
  return (
    <div className="did_you_know">
        <div className="did_you_know__logo">
<img src={didyouknow} alt='' />
        </div>
        <div className="did_you_know__text__1">
       <strong>{Fact.Fact1} </strong>
       </div>
       <div className="did_you_know__text__2">
       <strong>{Fact.Fact2} </strong>
        
        </div>
       {Loading ? ( <div className="content"><div class="load-wrapp">
         <div class="load-2">
           <div class="line"></div>
           <div class="line"></div>
           <div class="line"></div>
         </div>
       </div></div>):(Nothing ?(<h4><strong>Nothing to show</strong> </h4>):(<></>))}
        
       
    </div>
  )
}

export default DidYouKnow