import React, { useState, useEffect } from "react";
import { SimpleDropdown } from "react-js-dropdavn";
import "react-js-dropdavn/dist/index.css";
import MultiRangeSlider from "../Components/multiRangeSlider/MultiRangeSlider";
import "react-dropdown/style.css";
import "./SearchEngine.css";
import CourseShort from "../Components/search/CourseShort";
import DidYouKnow from '../Components/did_you_know/did_you_know'


const axios = require("axios")


const labels = {
  notFoundSearch: "Not found",
  notSelected: "Select any thing",
  selectedPrefix: "Your Choice :",
  search: "Search area",
  seachInputPlaceholder: "Search for typing"
};

function SearchEngine() {
  // For Refeshing Slider Values
  const [LoadSlider, setLoadSlider] = useState(false);
  // For Loading Animation
  const [IsLoading, setIsLoading] = useState(false);
  //  For Nothing to show
  const [ShowNothing, setShowNothing] = useState(true);
  // For Search
  const [Search, setSearch] = useState("");

  // For Get Filter Info
  const [ListCountries, setListCountries] = useState([]);
  const [ListDurationMin, setListDurationMin] = useState();
  const [ListDurationMax, setListDurationMax] = useState();
  const [ListDegree, setListDegree] = useState([]);
  const [ListDiscipline, setListDiscipline] = useState([]);
  const [ListFeeMin, setListFeeMin] = useState();
  const [ListFeeMax, setListFeeMax] = useState();
  const [ListInstitute, setListInstute] = useState([]);
  const [ListLanguages, setListLanguages] = useState([]);
  // For Apply Filters
  const [ShortCourseData, setShortCourseData] = useState([]);


  useEffect(() => {
    axios
      .get('http://127.0.0.1:5000/get_filter_options')
      .then(function (response) {
        // handle success 
        setListCountries(response.data[0].options)
        setListDurationMin(response.data[1].options[0])
        setListDurationMax(response.data[1].options[1])
        setListDegree(response.data[2].options)
        setListDiscipline(response.data[3].options)
        setListFeeMin(response.data[4].options[0])
        setListFeeMax(response.data[4].options[1])
        setListInstute(response.data[5].options)
        setListLanguages(response.data[6].options)
        setLoadSlider(true)

      })
      .catch(function (error) {
        // handle error
        console.log(error);
      })
      .then(function () {
        // always executed
      });

  }, []);

  let degree = "All",
    language = "All",
    country = "All",
    discipline = "All",
    semesterMin = 0,
    semesterMax = 99,
    tutionFeeMin = 0,
    tutionFeeMax = 999999999999;

  const applyFilter = (event) => {
    setIsLoading(true);
    setShowNothing(false)

    event.preventDefault();
    // insertArticle();
    axios.post('http://127.0.0.1:5000/apply_filter', {

      "search": Search,
      "limit": 100,
      "country": [country],
      "duration": [
        semesterMin, semesterMax
      ],
      "degree_level": [degree],
      "discipline": [discipline],
      "fee": [
        tutionFeeMin, tutionFeeMax
      ],
      "institute": ["All"],
      "language": [language]

    })
      .then(function (response) {
        // console.log(response);
        // console.log(response.data[0]);
        // console.log(response.data[0].intake_months[[0]]);
        // console.log(response);
        setShortCourseData(response.data)
        setIsLoading(false)
      })
      .catch(function (error) {
        console.log(error);
      });
  };

  return (
    <div className="SearchCources">
      <div className="idk">
        <div className="idk-again">
          <div className="SearchBoxDiv">
            <h1> Multiple Options, Multiple Choices</h1>
            <div className="SearchBox">
              <input onChange={(event) => {
                setSearch(event.target.value)

              }} type="text" />
              <button onClick={applyFilter} ><strong> Search </strong></button>
            </div>
          </div>
        </div>
        <div className="SearchBody">
          <div className="SearchResults">

            {IsLoading ? (
              <DidYouKnow state='Loading' />
            ) : (ShowNothing ? (<DidYouKnow state='Nothing' />) : (ShortCourseData.map((item, i) => (

              <CourseShort

                CourseID={item.course_id}
                Logo={item.logo}
                Course_Name={item.name}
                Course_Language={item.language}
                Course_DisplayName={item.level_type}
                Course_Degree_Type={item.deg_name}
                Course_Fee={item.curr_value}
                Course_Fee_Currency={item.curr_type}
                Institute_Name={item.ins_name}
                Course_Short_Description={item.description}
                Course_Intake={item.intake_month}
                Course_Deadline={!item.deadline_month}
                Course_Duration_Type={"Years"}
                Course_Duration={item.dur_year}
                Country_Name={item.address}
                Course_url={item.url}
              />

            )))
            )}




          </div>
          <div className="AdvanceFilters">
            <h4>Advance filters</h4>
            {/* Degree File*/}
            <div className="dropdown">
              <span><strong>Degree</strong></span>
              <SimpleDropdown
                options={ListDegree}
                searchable
                clearable
                onChange={(value) => (degree = value.label)}
                labels={labels}
                defaultValue="1"
                configs={{
                  position: {
                    y: "bottom",
                    x: "center"
                  },
                  fullWidthParent: true
                }}
                onClearSelected={() => console.log("selected is gone")}
                onDropdownOpened={() => console.log("opened")}
                onDropdownClosed={() => console.log("closed")}
                onDropdownInit={() => console.log("initialize")}
                onSearchTyping={({ search, filteredOptions }) => console.log(search, filteredOptions)} />
            </div>
            {/* Language Filter */}
            <div className="dropdown">
              <span><strong>Languages</strong></span>
              <SimpleDropdown
                options={ListLanguages}
                searchable
                clearable
                onChange={(value) => (language = value.label)}
                labels={labels}
                defaultValue="1"
                configs={{
                  position: {
                    y: "bottom",
                    x: "center"
                  },
                  fullWidthParent: true
                }}
                onClearSelected={() => console.log("selected is gone")}
                onDropdownOpened={() => console.log("opened")}
                onDropdownClosed={() => console.log("closed")}
                onDropdownInit={() => console.log("initialize")}
                onSearchTyping={({ search, filteredOptions }) => console.log(search, filteredOptions)} />
            </div>
            {/* Disipline */}
            <div className="dropdown">
              <span><strong>Discipline</strong></span>
              <SimpleDropdown
                options={ListDiscipline}
                searchable
                onChange={(value) => (discipline = value.label)}
                clearable
                labels={labels}
                defaultValue="1"
                configs={{
                  position: {
                    y: "bottom",
                    x: "center"
                  },
                  fullWidthParent: true
                }}
                onClearSelected={() => console.log("selected is gone")}
                onDropdownOpened={() => console.log("opened")}
                onDropdownClosed={() => console.log("closed")}
                onDropdownInit={() => console.log("initialize")}
                onSearchTyping={({ search, filteredOptions }) => console.log(search, filteredOptions)} />
            </div>
            {/* Countries*/}
            <div className="dropdown">
              <span><strong>Countries</strong> </span>
              <SimpleDropdown
                options={ListCountries}
                searchable
                clearable
                labels={labels}
                onChange={(value) => {
                  country = value.label;
                }}
                defaultValue="1"
                configs={{
                  position: {
                    y: "bottom",
                    x: "center"
                  },
                  fullWidthParent: true
                }}
                onClearSelected={() => console.log("selected is gone")}
                onDropdownOpened={() => console.log("opened")}
                onDropdownClosed={() => console.log("closed")}
                onDropdownInit={() => console.log("initialize")}
                onSearchTyping={({ search, filteredOptions }) => console.log(search, filteredOptions)} />
            </div>
            {/* Semester */}
            <div className="Semester">
              <strong> Years</strong>
              {LoadSlider ? (<MultiRangeSlider
                min={ListDurationMin
                }
                max={ListDurationMax}
                onChange={({ min, max }) => {
                  semesterMin = min;
                  semesterMax = max;
                }} />) : (<p>Error</p>)}

            </div>
            {/* Fee Filter */}
            <div className="FeeFilter">
              <span><strong>Tution Fee</strong> </span>
              {LoadSlider ? (
                <MultiRangeSlider
                  min={ListFeeMin}
                  max={ListFeeMax}
                  onChange={({ min, max }) => {
                    tutionFeeMin = min;
                    tutionFeeMax = max;
                  }} />
              ) : (<p>Error</p>)}

            </div>
            <button className="Apply_Filters" onClick={applyFilter}>
              <strong>Apply Filters</strong>
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default SearchEngine;
