import {React, useEffect, useState} from 'react'
import '../Components/accordian/accordian.css'
import './course_details.css'
import Question from '../Components/accordian/Question'
import { Link } from 'react-router-dom'
import back from './icons/back.png'
import logo from './icons/logored.png'

import {useLocation} from 'react-router-dom'

const axios = require("axios")

function Course_details() {
  const location = useLocation()
  const [CourseData,
    setCourseData] = useState([]);
  const [CourseLoaded,
    setCourseLoaded] = useState(false);

  const {ID} = location.state
  console.log(ID)



  useEffect(() => {
    axios
      .post('http://127.0.0.1:5000/get_course_info', {"course_id": ID})
      .then(function (response) {
        // console.log(response);
        console.log(response.data);
        setCourseData(response.data)
        setCourseLoaded(true)

      })
      .catch(function (error) {
        // handle error
        console.log(error);
      })
      .then(function () {
        // always executed
      });

  }, []);

  console.log("CourseLoaded:", CourseLoaded);


  return ( <> {
    CourseLoaded
      ? (
        <div className="more_details">

          <div className="yec_nav">
          <Link to='/'> <img src={back} alt="back" style={{ height:'8vh', background:'',marginInline:'20px', marginTop:'-5px'}} /></Link>
          <img src={logo} alt="logo"/>
         <div className="empty"></div>
          </div>
          <div className="more_div">
            <img src={CourseData.institute[5]} alt=""/>
            <div className="more_head">
              <h3>{CourseData.institute[0]}</h3>
              <h5>{CourseData.country}</h5>
            </div>
          </div>
          <div className="uni_img">
            <img src={process.env.PUBLIC_URL + `/Imgs/Uni${Math.floor(Math.random() *9 )}.jpg`} alt="" />
          </div>
          <div className="course_details">
            <div className="course_details_element">
              <h3>General</h3>
              <p><strong>{CourseData.discipline[1]}</strong> </p>
            </div>
          </div>
        
          <Question
            Title="Overview"
            HInfo0={"Campus :"}
            HInfo1={"Course Name :"}
            HInfo2={"Language :"}
            HInfo3={"Degree Level :"}
            HInfo4={"Degree Level :"}
            HInfo5={"Tution Fee :"}
            HInfo6={"In PKR :"}
            HInfo7={"Admission Months :"}
            HInfo8={"Admission Months :"}
            Info0={CourseData.campus}
            Info1={CourseData.course}
            Info2={CourseData.language}
            Info3={CourseData.degree_level[0]}
            Info4={CourseData.degree_level[1]}
            Info5={CourseData.currency_value[1] + ' ' + CourseData.currency_value[0]}
            Info6={CourseData.fee_in_pkr}
            Info7={CourseData.intake_months[0]}
            Info8={CourseData.intake_months[4]}
            />
          <Question
            Title="Ranking"
            HInfo0={"QS World Ranking :"}
            HInfo1={"Us News World Ranking :"}
            HInfo2={"Times Higher World Ranking :"}
            HInfo3={"Shanghai Ranking :"}
            Info0={CourseData.ranking[0] ? CourseData.ranking[0][0] : ""  } 
            Info1={CourseData.ranking[1] ? CourseData.ranking[1][0] : ""  } 
            Info2={CourseData.ranking[2] ? CourseData.ranking[2][0] : ""  } 
            Info3={CourseData.ranking[3] ? CourseData.ranking[3][0] : ""  } 
            
        
          />
            <Question
            Title="About Institute"
            HInfo0={"Institute Name : "}
            HInfo1={"Established in : "}
            HInfo2={"Type : "}
            HInfo3={"Institue Type : "}
            HInfo4={"City : "}
            

            Info0={CourseData.institute[0]}
            Info1={CourseData.institute[1]}
            Info2={CourseData.institute[2]}
            Info3={CourseData.institute[3]}
            Info4={CourseData.city}
            />
         <a href={CourseData.url}> <button className='CourseDetailsApply'>Apply With YEC</button> </a>
        </div>

      )
      : (<> not i </>)
  }
  </>)
  }

  export default Course_details