import React from 'react'
import './CourseShort.css'
import { Link } from 'react-router-dom'
import calender from './icons/calender.png'
import clock from './icons/clock.png'
import location from './icons/location.png'
import language from './icons/lang.png'
import course from './icons/course.png'
import courses from './icons/courses.png'
import { colors } from '@mui/material'
function CourseShort(props) {
  const ID=props.CourseID;
  return (
    <div className="CourseShort">
      <div className="course_top">
        <img className='Uni_Logo' src={props.Logo} alt="" />
        <span><strong>{props.Course_Name}</strong> </span>
        <span><strong>{props.Institute_Name}</strong> </span>
      </div>
      <div className="course_text">
        <span>{props.Course_Short_Description}</span>
      </div>
      <div className="course_detail">
        <div className="course_details_row">
          <div className="course_details_col">
            <div className="course_details_statment">
              <img src={calender} alt=""/>
              <span><strong>{props.Course_Intake}-{props.Course_Deadline}</strong></span>
            </div>
            <div className="course_details_statment">
              <img src={clock} alt=""/>
              <span><strong>{props.Course_Duration} {props.Course_Duration_Type}</strong></span>
            </div>
            <div className="course_details_statment">
              <img src={location} alt=""/>
              <span><strong>{props.Country_Name}</strong></span>
            </div>
          </div>
          <div className="course_details_col">
            
            <div className="course_details_statment">
              <img src={course} alt=""/>
              <span><strong>{props.Course_DisplayName}</strong></span>
            </div>
            <div className="course_details_statment">
              <img src={courses} alt=""/>
              <span><strong>{props.Course_Degree_Type}</strong></span>
            </div>
            <div className="course_details_statment">
             
              <span></span>
            </div>
          </div>
          <div className="course_details_col">
            <div className="course_details_statment">
              <img src={language} alt=""/>
              <span><strong>{props.Course_Language}</strong></span>
            </div>
            <div className="course_details_statment">
              <img src={location} alt=""/>
              <span><strong>{props.Course_Fee} {props.Course_Fee_Currency}</strong></span>
            </div>
            <div className="course_details_statment">
            
              <span></span>
            </div>
          </div>
          
             <div className="course_buttons">
          <Link className="more_button" to="/More" state={{ ID: ID }}>More Details</Link>
          <button>
  <a href={props.Course_url} style={{ color: 'white' }}>
    <strong>Apply with YEC</strong>
  </a>
</button>

 
            
          </div>
        </div>

      </div>

    </div>
  )
}

export default CourseShort