import React, {useState} from 'react'
import {AiOutlineMinus, AiOutlinePlus} from 'react-icons/ai'

function Question(props) {

  const [expanded,
    setExpanded] = useState(false)

  return (
    <article className='question'>
      <header>
        <h4 onClick={() => setExpanded(!expanded)} className='question-title'>
          {props.Title}
        </h4>
        <button className='btn' onClick={() => setExpanded(!expanded)}>
          {expanded
            ? <AiOutlineMinus/>
            : <AiOutlinePlus/>}
        </button>
      </header>
      {expanded && <div className='accoridan_values'>
        <p><strong>{props.HInfo0}</strong>{props.Info0}</p>
        <p><strong>{props.HInfo1}</strong>{props.Info1}</p>
        <p><strong>{props.HInfo2}</strong>{props.Info2}</p>
        <p><strong>{props.HInfo3}</strong>{props.Info3}</p>
        <p><strong>{props.HInfo4}</strong>{props.Info4}</p>
        <p><strong>{props.HInfo5}</strong>{props.Info5}</p>
        <p><strong>{props.HInfo6}</strong>{props.Info6}</p>
        <p><strong>{props.HInfo7}</strong>{props.Info7}</p>
      </div>}
    </article>
  )
}

export default Question
