import React from 'react';
import { Link } from 'react-router-dom';

export default function Navbar() {
  return (
    <div>
    <head>
  <meta charset="utf-8"/>
  <meta content="width=device-width, initial-scale=1.0" name="viewport"/>
  <link href="https://cdn.jsdelivr.net/npm/bootstrap@5.3.2/dist/css/bootstrap.min.css" rel="stylesheet" integrity="sha384-T3c6CoIi6uLrA9TneNEoa7RxnatzjcDSCmG1MXxSR1GAsXEV/Dwwykc2MPK8M2HN" crossorigin="anonymous"></link>
  <title>Mentor Bootstrap Template - Index</title>
  <meta content="" name="description"/>
  <meta content="" name="keywords"/>
</head>
<nav
  className="navbar navbar-expand-lg bg-body-tertiary position-fixed top-0 w-100"
  style={{ zIndex: 1000 }}
>
  <a className="navbar-brand mx-5" style={{ color: "cornflowerblue" }} href="#">
    YoursEduConnect
  </a>
  <div className="container-fluid">
    <button
      className="navbar-toggler"
      type="button"
      data-bs-toggle="collapse"
      data-bs-target="#navbarSupportedContent"
      aria-controls="navbarSupportedContent"
      aria-expanded="false"
      aria-label="Toggle navigation"
    >
      <span className="navbar-toggler-icon" />
    </button>
    <div className="collapse navbar-collapse px-5" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <a
            className="nav-link active mx-3"
            aria-current="page"
            href="http://127.0.0.1:5000/home"
          >
            Home
          </a>
        </li>
        <li className="nav-item">
          <a
            className="nav-link active mx-3"
            href="http://127.0.0.1:5000/about"
          >
            About
          </a>
        </li>
        <li className="nav-item">
          <a
            className="nav-link active mx-3"
            href="http://127.0.0.1:5000/filter"
          >
            Courses
          </a>
        </li>
        <li className="nav-item">
          <a
            className="nav-link active mx-3"
            href="http://127.0.0.1:5000/contact"
          >
            Contact
          </a>
        </li>
      </ul>
      <a href="http://127.0.0.1:5000/signin">
        <button className="btn btn-dark w-100 round-circle mx-auto">
          Logout
        </button>
      </a>
    </div>
  </div>
</nav>

  <script src="https://cdn.jsdelivr.net/npm/bootstrap@5.3.2/dist/js/bootstrap.bundle.min.js" integrity="sha384-C6RzsynM9kWDrMNeT87bh95OGNyZPhcTNXj1NW7RuBCsyN/o0jlpcV8Qyq46cDfL" crossorigin="anonymous"></script>


    </div>
  )
}
